*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --primary-font: "Manrope";
  --accent-color: #4440f6;
  --text-status-color: #00dd73;
  --text-color-white: #fff;
  --background-color-white: #fff;
  --primary-text-color-light-theme: #111321;
  --primary-background-color-light-theme: #f4f4f4;
  --primary-text-color-dark-theme: #f4f4f4;
  --background-color-dark-theme: #2e2e2e;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(0, 0, 0, .2);
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 450px;
  background-color: var(--primary-text-color-dark-theme);
  border-radius: 4px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
  }
}

@media screen and (max-width: 480px) {
  .modal {
    width: calc(100% - 30px);
  }
}

.modal__close-button {
  width: 30px;
  height: 30px;
  background-color: var(--primary-text-color-dark-theme);
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  outline: none;
  padding: 0;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close-button:hover .modal__close-icon, .modal__close-button:focus .modal__close-icon {
  fill: var(--accent-color);
  background-color: var(--primary-text-color-dark-theme);
  border-color: var(--accent-color);
}

.modal__close-icon {
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__title {
  text-align: center;
  letter-spacing: .03em;
  color: var(--primary-text-color);
  margin-top: 12px;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.order-form__lable {
  letter-spacing: .01em;
  color: var(--title-text-color);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
  display: block;
}

.order-form__block {
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

.order-form__block:hover .order-form__icon, .order-form__block:focus-within .order-form__icon {
  fill: var(--accent-color);
}

.order-form__icon {
  fill: currentColor;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.order-form__input {
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 11px 12px 11px 42px;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.order-form__input:hover, .order-form__input:focus {
  border-color: var(--accent-color);
  background-color: var(--background-color-white);
  cursor: pointer;
}

.modal-button {
  background-color: var(--background-color-white);
  color: var(--primary-text-color);
  cursor: pointer;
  font-family: var(--primary-font);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 20px;
  outline: none;
  padding: 8px 21px;
  display: flex;
}

.modal-button:hover, .modal-button:focus {
  box-sizing: border-box;
  border-color: var(--accent-color);
  color: var(--accent-color);
}

.block-button {
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
  display: flex;
}

.dark-text {
  color: #5f6775;
}

.modal-dark {
  color: #f4f4f4;
  background-color: #2e2e2e;
}

.modal-button.modal-dark {
  color: #f4f4f4;
  border-color: #f4f4f4;
}

.order-form__input.modal-dark {
  color: #f4f4f4;
  border: 1px solid #f4f4f4;
}

.order-form__input.modal-dark:hover, .order-form__input.modal-dark:focus {
  color: #f4f4f4;
  background-color: #2e2e2e;
  border-color: #f4f4f4;
}

.order-form__input.modal-dark {
  color: #f4f4f4;
  border: 1px solid #f4f4f4;
}

.modal__close-button.modal-dark {
  color: #f4f4f4;
  fill: #f4f4f4;
  border: 1px solid #f4f4f4;
}

.modal-err-message {
  color: red;
  text-align: center;
}

.modal-ok-message {
  color: green;
  text-align: center;
}

