.nav__link--current:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #4440f6;
  animation-name: current-underline;
  animation-duration: .35s;
  animation-timing-function: linear;
  display: block;
  position: absolute;
  bottom: -7px;
}

@keyframes current-underline {
  0% {
    transform: scaleX(0);
  }

  50% {
    transform: scaleX(.5);
  }

  100% {
    transform: scaleX(1);
